    margin:0;
    padding:0;
    list-style:none
}
a {
    background-color:transparent;
    text-decoration:none
}
img {
    border:0
}
table {
    border-collapse:collapse;
    border-spacing:0
}
td, th {
    padding:0
}
button, input, select, textarea {
    color:inherit;
    font:inherit;
    margin:0
}
textarea {
    overflow:auto
}
