    display:block
}
.hidden2, .hidden3, .hidden4, .hidden5 {
    display:-webkit-box;
    -webkit-box-orient:vertical;
    overflow:hidden
}
.hidden1 {
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap
}
.hidden2 {
    -webkit-line-clamp:2
}
.hidden3 {
    -webkit-line-clamp:3
}
.hidden4 {
    -webkit-line-clamp:4
}
.hidden5 {
    -webkit-line-clamp:5
}
.clearfix:after {
    content:"";
    width:0;
    height:0;
    display:block;
    clear:both
}
